<template>
	<div id="deliverys_manager">
		<div v-if="mode==='list'">
			<div class="bareBtnCtnr">
				<button
					@click="mode='create'"
					class="btn btn-primary"
				><font-awesome-icon icon="plus" class="textAside" />新增</button>
			</div>
			<template v-for="(item, index) in deliverys">
				<h4>配送{{ index+1 }}</h4>
				<delivery-manager
					:pk="item.id"
					:key="index"
					@create=""
					@update="update_message"
					@delete="delete_message"
				></delivery-manager>
			</template>
		</div>
		<div v-if="mode==='create'">
			<div class="bareBtnCtnr">
				<button
					@click="mode='list'"
					class="btn btn-primary"
				><font-awesome-icon icon="stream" class="textAside" />回列表</button>
			</div>
			<delivery-manager :pk="0" ref="pc"></delivery-manager>
			<div class="bareBtnCtnr">
				<button
					@click="create"
					class="btn btn-primary"
				><font-awesome-icon icon="plus" class="textAside" />建立</button>
			</div>
		</div>
	</div>
</template>

<script>
import { deliveryRest, order_rest as orderRest } from '@/api/sale';
import deliveryManager from './delivery_manager.vue';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
	},
	name: 'deliverys_manager',
	components: {
		deliveryManager,
	},
	data() {
		return {
			id: -Math.floor(Math.random() * 100000000).toString(),
			mode: 'list',
			order: {},
			deliverys: [],
		};
	},
	computed: {
	},
	async mounted() {
		await this.load();
	},
	methods: {
		async load(){
			this.mode = 'list';
			this.order = {};
			this.deliverys = [];
			let res;
			try {
				res = await orderRest.read(this.pk);
				this.order = res.data;
				res = await this.deliverys_read_promise();
				res.forEach((v, i)=>  {
					this.deliverys.push(v.data);
				})
			}
			catch (error) {
				console.log(error);
			}
		},
		async create(){
			this.$refs['pc'].order = this.order.id;
			this.$refs['pc'].installment = this.order.installment;
			let res;
			try {
				res = await this.$refs['pc'].operate('create');

				this.$root.$message.open(0, '成功建立配送' +res.data.id);
				await this.load();
			}
			catch (error) {
				if(error.status){
					this.$root.$message.open(error.status, error.data);
				} else {
					console.log(error);
					this.$root.$message.open(-1, error);
				}
			}
		},
		update_message(event){
			this.load();
			this.$root.$message.open(0, '成功儲存配送' +event.id, '');
		},
		async delete_message(event){
			this.load();
			this.$root.$message.open(0, '成功刪除配送' +event.id, '');
		},
		deliverys_read_promise(){
			let axiosapis = [];
			this.order.delivery_set.forEach((v, i)=>  {
				axiosapis.push(deliveryRest.read(v));
			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style scoped lang="scss">
</style>



