<template>
	<div :id="'delivery_manager' +this.id">
		<div v-if="pk==0">編號：新方案</div>
		<div v-else>編號：{{ pk }}</div>
		<div v-if="mode=='read'">
			<div class="bareBtnCtnr">
				<button
					@click="mode='write'"
					class="btn btn-primary"
				><font-awesome-icon icon="pen" class="textAside" />編輯</button>
			</div>
			<div>姓名：{{ name }}</div>
			<div>地址：{{ address }}</div>
			<div>郵遞區號：{{ zip }}</div>
			<div>聯絡電話：{{ phone }}</div>
			<div>性別：
				<template v-if="sex==='man'">
					男
				</template>
				<template v-if="sex==='woman'">
					女
				</template>
			</div>
			<div>公司單位：{{ company }}</div>
			<div>出貨日期：{{ date }}</div>
			<div>出貨編號：{{ number }}</div>
		</div>
		<div v-if="mode=='write'" class="container">
			<div class="bareBtnCtnr">
				<button
					@click="cancel"
					class="btn btn-primary"
				><font-awesome-icon icon="times" class="textAside" />取消</button>
			</div>
			<formdrf
				:model_info="model_info.name"
				:field="'name'"
				:offset-class="'col-sm-offset-3'"
				v-model="name"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.address"
				:field="'address'"
				:offset-class="'col-sm-offset-3'"
				v-model="address"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.zip"
				:field="'zip'"
				:offset-class="'col-sm-offset-3'"
				v-model="zip"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.company"
				:field="'company'"
				:offset-class="'col-sm-offset-3'"
				v-model="company"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.phone"
				:field="'phone'"
				:offset-class="'col-sm-offset-3'"
				v-model="phone"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.sex"
				:field="'sex'"
				:offset-class="'col-sm-offset-3'"
				v-model="sex"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.date"
				:field="'date'"
				:offset-class="'col-sm-offset-3'"
				v-model="date"
				@keyup.enter.native=""
			></formdrf>
			<formdrf
				:model_info="model_info.number"
				:field="'number'"
				:offset-class="'col-sm-offset-3'"
				v-model="number"
				@keyup.enter.native=""
			></formdrf>
			<div v-if="pk===0">
				<!--<button @click="operate('create')">建立</button>-->
			</div>
			<div v-else>
				<div class="bareBtnCtnr">
					<button
						@click="operate('update')"
						class="btn btn-primary"
					><font-awesome-icon icon="sync-alt" class="textAside" />儲存</button>
					<button
						@click="operate('delete')"
						class="btn btn-secondary"
					><font-awesome-icon icon="trash-alt" class="textAside" />刪除</button>
				</div>
			</div>
		</div>
		<product-deliverys-manager :pk="pk"></product-deliverys-manager>
	</div>
</template>

<script>
import formdrf from './form.vue';
import tableDiv from './table_split.vue';
import { deliveryRest, productdeliveryRest, product_rest as productRest } from '@/api/sale';
import productDeliverysManager from './product_deliverys_manager.vue';

export default {
	props: {
		pk: {
			type: Number,
			default: 0,
		},
	},
	name: 'delivery_manager',
	components: {
			formdrf,
		tableDiv,
		productDeliverysManager,
	},
	data() {
		return {
			id: -Math.floor(Math.random() * 100000000).toString(),
			sequence100: [...Array(100).keys()],
			mode: 'read',
			product_list: [], // API 商品列表
			delivery: {
				id: 0,
				order: null,
				name: '',
				address: '',
				zip: '',
				phone: '',
				sex: 'man',
				company: '',
				date: null,
				number: null,
				productdelivery_set: [],
			}, // API 取得 delivery
			name: '',
			address: '',
			zip: '',
			phone: '',
			sex: 'man',
			company: '',
			date: null,
			number: null,
			order: null,
			model_info: {
				name: {
					'label': '姓名',
					'type': 'text',
					required: true,
				},
				address: {
					'label': '地址',
					'type': 'text',
					required: true,
				},
				zip: {
					'label': '郵地區號',
					'type': 'text',
					required: true,
				},
				company: {
					'label': '公司/單位',
					'type': 'text',
				},
				phone: {
					'label': '聯絡電話',
					'type': 'text',
					required: true,
				},
				sex: {
					'label': '性別',
					'type': 'select',
						'choices' : [
							{
								'value': 'man',
								'display_name': '男',
							},
							{
								'value': 'woman',
								'display_name': '女',
							},
						],
				},
				date: {
					'label': '出貨日期',
					'type': 'text',
					required: false,
				},
				number: {
					'label': '出貨編號',
					'type': 'text',
					required: false,
				},
			},
			product_count: [], // 商品數量
			product_count_header: {
				name: '名稱',
				count: '數量',
			},
		};
	},
	computed: {
		product_count_datas(){
			return this.product_count;
		},
		deliveryUpdate(){
			return {
				"id": this.pk,
				name: this.name,
				address: this.address,
				zip: this.zip,
				phone: this.phone,
				company: this.company,
				date: this.date,
				number: this.number,
				order: this.order,
			}
		},
		productdeliverysUpdate(){
			let temp = []
			let c=0;
			this.product_count.forEach((v, i)=>	{
				if((!(v.count===0)) || v.id){
					temp.push({
						id: v.id,
						count: v.count,
						delivery: this.pk,
						product: v.product,
					})
					c=c+1;
				}
			})
			return temp
		},
	},
	watch: {
	},
	async mounted() {
		let res;
		try {
			res = await Promise.all([
				productRest.list(),
			])
			this.product_list = res[0].data;
			if(this.pk){
				res = await deliveryRest.read(this.pk)
				this.delivery = res.data;
			}
			else {
				this.mode = 'write'
			}
			this.delivery2data();
		}
		catch (error) {
			console.log(error);
		}

	},
	methods: {
		out(){
			let delivery = this.deliveryUpdate;
			delivery = _.merge(delivery, {productdelivery_set: this.productdeliverysUpdate,});
			this.$emit('update', delivery);
		},
		default_product_count(){
			this.product_count = [];
			this.product_list.forEach((v, i)=>	{
				this.product_count.push({
					product: v.id,
					id: null,
					name: v.name,
					count: 0,
				})
			});
		},
		delivery2data(){
			this.pk = this.delivery.id;
			this.order = this.delivery.order;
			this.name = this.delivery.name;
			this.address = this.delivery.address;
			this.zip = this.delivery.zip;
			this.phone = this.delivery.phone;
			this.company = this.delivery.company;
			this.sex = this.delivery.sex;
			this.date = this.delivery.date;
			this.number = this.delivery.number;
			this.default_product_count();
			this.delivery.productdelivery_set.forEach((vp, ip)=>{
				this.product_count.forEach((vpc, ipc)=>{
					if(vp.product==vpc.product){
						vpc.id=vp.id;
						vpc.count=vp.count;
					}
				})
			})
		},
		async operate(action){

			if(this.date===''){
				this.date=null;
			}

			let res;
			if(action=='create'){
				try {
					res = await deliveryRest.create(this.deliveryUpdate);
					this.pk = res.data.id;
					res = await this.productdeliverys_update_promise();
					res = await deliveryRest.read(this.pk);
					this.delivery = res.data;
					this.delivery2data();
					this.$emit('create', res.data);
					return res;
				}
				catch(error){
					throw(error.response);
				}
			}
			else if(action=='read'){
				try {
					res = await deliveryRest.read(this.pk)
					this.delivery = res.data;
					this.delivery2data();
				}
				catch (error) {
					console.log(JSON.stringify(error.response));
				}
			}
			else if(action=='update'){
				try {
					res = await deliveryRest.update(this.pk, this.deliveryUpdate);
					res = await this.productdeliverys_update_promise();
					res = await deliveryRest.read(this.pk);
					this.delivery = res.data;
					this.delivery2data();
					this.$emit('update', res.data);
					return res;
				}
				catch (error) {
					throw(error.response);
				}
			}
			else if(action=='delete'){
				try {
					await this.$root.$confirm.open(0, '確定刪除此項目？');
					res = await deliveryRest.delete(this.pk)
					this.$emit('delete', {id: this.pk});
					return res;
				}
				catch (error) {
					throw(error.response);
				}
			}
		},
		cancel(){
			this.delivery2data();
			this.mode = 'read';
		},

		productdeliverys_update_promise(){
			let axiosapis = [];
			this.productdeliverysUpdate.forEach((v, i)=>	{
				if(v.id){
					if(v.count>0){
						axiosapis.push(productdeliveryRest.update(v.id, v));
					}
					else{
						axiosapis.push(productdeliveryRest.delete(v.id));
					}
				}
				else{
					axiosapis.push(productdeliveryRest.create(v));
				}

			})
			return Promise.all(axiosapis);
		},
	},
};
</script>

<style scoped lang="scss">
	@import '../style/base.scss';

	.proposalManager {
		// .This class is too specific and only served for fixing specific styling.
		// .Also see comment id 190509m181803.
		overflow: auto;
	}
	.proposalDescriptionCtnr {
		margin-top: $paddingTopBtmDefault;
	}
</style>



